<template>
  <div class="yuedu_footer">
    <div class="addBookrack" v-show="!bookstate" @click.stop="addBookFn"></div>
    <div class="deleteBookrack" v-show="bookstate" @click.stop="deleteBookFn"></div>
    <div class="control">
      <div class="progressBar">
        <div class="sub" @click.stop="subFontSize">Aa-</div>
        <div class="add" @click.stop="addFontSize">Aa+</div>
        <div class="progress">
          <div class="bar" :style="{width: jindu(fontSize)+'%'}"></div>
        </div>
      </div>
      <div class="control_button">
        <div
          v-for="(item, index) in moshiArr"
          :key="index"
          :class="item.classObj"
          @click.stop="moshiTab(index)"
        >{{item.content}}</div>

        <!-- #0c0c0c h4 #888 p #666 -->

        <!-- #b8cd8d #0c2e10  rgba(12,46,16,.8) -->
      </div>
      <div class="control_section">
        <div
          class="previousChapter"
          :class="{disabled: index === 0}"
          @click.stop="previousChapterFn"
        ></div>
        <div class="nextChapter" @click.stop="nextChapterFn"></div>
        <div class="catalogue" @click.stop="showCatalogueFn"></div>
      </div>
    </div>
  </div>
</template>

<script>
export default {
  // 
  props: ["index"],
  data() {
    return {
      fontSize: 20,
      moshiArr: [
        {
          classObj: {
            default: true,
            active: true
          },
          content: ""
        },
        {
          classObj: {
            night: true,
            active: false
          },
          content: ""
        },
        {
          classObj: {
            eyeshield: true,
            active: false
          },
          content: ""
        }
      ]
    };
  },
  watch: {
    //   
    fontSize() {
      this.$emit("setData", { key: "fontSize", value: this.fontSize });
    }
  },
  computed: {
    bookstate() {
      return this.$store.getters.getBookById(this.$route.query.id);
    }
  },
  methods: {
    addBookFn() {
      //   ()vuex
      //   ,
      // 
      // +1
      var index = this.index+1;

      this.$store.commit("addBookrack", {
        title: this.$route.query.title,
        cover: this.$route.query.title,
        id: this.$route.query.id,
        index: index ? index : 1,
        // time:
        //   this.formating(Date.now() - Date.parse(this.data.updated)) +
        //   " : " +
        //   this.data.lastChapter
      });
    },

    deleteBookFn() {
      this.$store.commit("deleteBook", this.$route.query.id);
    },
    formating(str) {
      var miao = str / 1000;
      var fen = miao / 60;
      var xiaoshi = fen / 60;
      var tian = xiaoshi / 24;

      if (tian > 1) {
        return parseInt(tian) + "";
      }

      if (xiaoshi >= 1) {
        return parseInt(xiaoshi) + "";
      } else if (fen >= 1) {
        return parseInt(fen) + "";
      }
    },
    // ++index-1-2+1
    // index1
    previousChapterFn() {
      if (this.index > 0) this.$emit("setData", { key: "index", value: "--" });
    },
    nextChapterFn() {
      this.$emit("setData", { key: "index", value: "++" });
    },
    //   
    showCatalogueFn() {
      window.console.log("");
      this.$emit("setData", { key: "control", value: false });
      this.$emit("setData", { key: "showCatalogue", value: true });
    },
    // tab
    moshiTab(index) {
      for (let i = 0; i < this.moshiArr.length; i++) {
        this.moshiArr[i].classObj.active = false;
      }
      this.moshiArr[index].classObj.active = true;
      //   classObj
      this.$emit("setData", {
        key: "classObj",
        value: this.moshiArr[index].classObj
      });
    },
    addFontSize() {
      this.fontSize += 2;
    },
    subFontSize() {
      this.fontSize -= 2;
    },
    jindu(num) {
      switch (num) {
        case 16:
          return 0;
        case 18:
          return 25;
        case 20:
          return 50;
        case 22:
          return 75;
        case 24:
          return 100;
      }
    }
  }
};
</script>

<style lang='less' scoped>
.yuedu_footer {
  .addBookrack,
  .deleteBookrack {
    position: absolute;
    top: -40px;
    right: 0px;
    border-radius: 20px 0px 0px 20px;
    width: 70px;
    height: 25px;
    background-color: #333;
    color: #fff;
    font-size: 12px;
    line-height: 15px;
    padding: 5px;
  }
  .control {
    background-color: rgba(50, 51, 52, 0.9);
    width: 100%;
    height: auto;
    padding: 20px;
    .progressBar {
      width: 100%;
      &::after {
        content: "";
        display: block;
        clear: both;
      }
      padding: 0 40px;
      .sub,
      .add {
        color: #fff;
        float: left;
        font-size: 17px;
        margin-left: -40px;
      }
      .add {
        float: right;
        margin-right: -40px;
      }
      .progress {
        background-color: #999;
        height: 2px;
        position: relative;
        top: 11px;
        .bar {
          position: absolute;

          height: 2px;
          background-color: #b93221;
        }
      }
    }

    .control_button {
      // width: 100%;
      margin: 15px 0;
      display: flex;
      justify-content: space-around;
      .default,
      .night,
      .eyeshield {
        width: 80px;
        height: 30px;
        border: 1px solid #999;
        float: left;
        margin-right: 10px;
        color: #fff;
        line-height: 28px;
        border-radius: 3px;
      }
      .active {
        color: #b93221;
        border-color: #b93221;
      }
    }

    .control_section {
      margin-top: 10px;
      &::after {
        content: "";
        display: block;
        clear: both;
      }
      .previousChapter,
      .nextChapter,
      .catalogue {
        color: #fff;
      }
      .previousChapter {
        float: left;
        &.disabled {
          color: #888;
        }
      }
      .nextChapter {
        float: right;
      }
      .catalogue {
        color: #999;
      }
    }
  }
}
</style>