<template>
  <div class="SearchInterface">
    <!--  -->
    <Headshot :title="title"></Headshot>

    <!-- vuexvalue -->
    <!--  -->
    <div class="Search">
      <div class="Search-box">
        <div class="input">
          <div class="icon-search"></div>
          <input type="text" placeholder="" @input="inputFn" v-model.trim="value" />
          <div class="icon-delete" v-show="value" @click.stop="deleteClickFn"></div>
        </div>
        <!--   -->
        <div class="btn" @click.stop="routegoSearchListClickFn"></div>
      </div>
    </div>
    <!--  -->
    <div class="search-list">
      <ul>
        <li
          v-for="(item, index) in list"
          :key="index"
          @click.stop="routegoSearchListClickFn"
        >{{item}}</li>
      </ul>
    </div>

    <!--  -->
    <Searchhot />
    <!--  -->
    <SearchHistory />
  </div>
</template>
<script>
import Headshot from "../components/headshot";
import Searchhot from "../components/Searchhot";
import SearchHistory from "../components/SearchHistory";
export default {
  name: "Search",
  components: {
    Headshot,
    Searchhot,
    SearchHistory
  },
  data() {
    return {
      title: "",
      list: [],
      value: ""
    };
  },
  methods: {
    inputFn() {
      window.console.log(
        "http://novel.kele8.cn/auto-complete?query=" + this.value
      );
      if (this.value) {
        this.axios
          .get("http://novel.kele8.cn/auto-complete?query=" + this.value)
          .then(response => {
            window.console.log(response);
            this.list = response.data.keywords;
          });
      } else {
        this.list = [];
      }
    },
    deleteClickFn() {
      this.value = "";
      this.list = [];
    },
    routegoSearchListClickFn() {
      // 
      // 
      if (!localStorage.Search) {
        window.console.log("");
        let Search = { KeyWords: [] };
        Search.KeyWords.push(this.value);
        // 
        localStorage.setItem("Search", JSON.stringify(Search));
      } else {
        let Search = JSON.parse(localStorage.getItem("Search"));
        for (let i = 0; i < Search.KeyWords.length; i++) {
          if (this.value === Search.KeyWords[i]) {
            Search.KeyWords.splice(i, 1);
          }
        }
        Search.KeyWords.push(this.value);
        // 
        localStorage.setItem("Search", JSON.stringify(Search));
      }

      // 
      this.$router.push({
        path: "/SearchList",
        query: {
          value: this.value
        }
      });
    }
  }
};
</script>
<style lang="less">
.SearchInterface {
  .Search {
    width: 100%;
    height: 0.93333rem;
    box-sizing: border-box;
    padding: 0 15px;
    margin: 10px 0;
    .Search-box {
      display: flex;
      width: 100%;
      height: 100%;
      .input {
        flex: 1;
        height: 100%;
        background-color: #ebebf0;
        border-radius: 0.46667rem;
        padding: 5px 10px;
        position: relative;
        input {
          float: left;
          height: 100%;
          background-color: transparent;
          border: none;
          outline: none;
          line-height: 0.93333rem;
        }
        .icon-search {
          float: left;
          width: 0.6rem;
          height: 100%;
          background-image: url(../assets/search.png);
          background-repeat: no-repeat;
          background-size: 0.6rem 0.6rem;
          margin-right: 1px;
        }
        .icon-delete {
          width: 0.4rem;
          height: 0.4rem;
          background-image: url(../assets/search-delete.png);
          background-repeat: no-repeat;
          background-size: 100% 100%;

          position: absolute;
          right: 8px;
          top: 50%;
          margin-top: -0.2rem;
        }
      }
      .btn {
        width: 42px;
        height: 100%;
        color: #748389;
        line-height: 15px;
      }
    }
  }
  .search-list {
    width: 100%;
    padding: 0 15px;
    ul {
      width: 100%;
      li {
        position: relative;
        width: 100%;
        height: 1.38667rem;
        line-height: 1.38667rem;
        text-align: left;
        padding-left: 0.52667rem;
        &::before {
          content: "";
          display: block;
          width: 0.42667rem;
          height: 0.42667rem;
          background-image: url(../assets/searchlist-icon.png);
          background-repeat: no-repeat;
          background-size: 0.42667rem 0.42667rem;

          position: absolute;
          left: 0;
          top: 50%;
          margin-top: -0.21333rem;
        }
      }
    }
  }
}
</style>