<template>
  <div :class="{showControl: control}">
    <van-loading id="yueduqiLoading" size="24px" vertical v-show="startloading">...</van-loading>
    <div class="top">
      <yueduHead :title="info.title" :id="$route.query.id" />
    </div>

    <div class="yuedubook" :class="classObj" @click.stop="yuedubookClickFn">
      <van-list
        :error.sync="error"
        error-text=""
        v-model="loading"
        :finished="finished"
        finished-text=""
        @load="onLoad"
      >
        <van-cell v-for="(item, index) in list" :key="index">
          <h2 :style="{fontSize: (fontSize+4)+'px'}">{{item.title}}</h2>
          <div class="yuedubook-box">
            <p
              v-for="(content, index) in item.cpContent"
              :key="index"
              :style="{fontSize: fontSize+'px'}"
            >{{content}}</p>
          </div>
          <div class="erweima"></div>
        </van-cell>
      </van-list>
    </div>
    <div class="bottom">
      <yuduFooter @setData="setData" :index="page" />
    </div>
    <div class="cataloguejiemian" :class="{show: showCatalogue}">
      <yueduCatalogue
        :chapters="zhangjielist"
        @setData="setData"
        :obj="{ title:info.title,
                index: this.page,}"
      />
    </div>
  </div>
</template>

<script>
import Vue from "vue";
import yueduHead from "../components/yueduHead";
import yuduFooter from "../components/yueduFooter";
import yueduCatalogue from "../components/yueduCatalogue";
export default {
  data() {
    return {
      error: false,
      info: {},
      startloading: true,
      zhangjielist: null,
      // 
      index: 0,
      // 
      page: 0,
      list: [],
      loading: false,
      finished: false,

      control: false,
      showCatalogue: false,
      fontSize: 20,
      classObj: {
        default: true,
        night: false,
        eyeshield: false
      }
    };
  },
  components: {
    yueduHead,
    yuduFooter,
    yueduCatalogue
  },
  methods: {
    yuedubookClickFn() {
      if (this.showCatalogue) {
        this.showCatalogue = !this.showCatalogue;
      } else {
        this.control = !this.control;
      }
    },
    setData({ key, value }) {
      if (key === "index") {
        window.console.log("index", value);
        // page indexindex
        switch (value) {
          case "--":
            this[key] -= 2;
            this.page = this[key];
            break;
          case "++":
            // this[key] = ++this[key];
            this.page = this[key];
            break;
          default:
            this[key] = value;
            this.page = value;
            break;
        }
        

        this.list = [];
        this.onLoad();
      } else {
        this[key] = value;
      }
    },
    updatabookrack() {
      // 
      var bookrack = JSON.parse(localStorage.getItem("bookrack"));
      if (bookrack) {
        window.console.log("", bookrack);
        // 
        // 
        let ischange = true;
        // 
        for (let i = 0; i < bookrack.length; i++) {
          if (bookrack[i].id === this.$route.query.id) {
            window.console.log("");
            // 
            ischange = false;
            bookrack[i].time = new Date();
            bookrack[i].index = this.index;
            bookrack.push(bookrack.splice(i, 1)[0]);

            break;
          }
        }
        // 
        if (ischange) {
          window.console.log("");
          bookrack.push({
            time: new Date(),
            index: this.index,
            id: this.$route.query.id,
            title: this.info.title,
            cover: window.unescape(this.info.cover).slice(7)
          });
        }
      } else {
        window.console.log("");
        // 
        bookrack = [
          {
            time: new Date(),
            index: this.index,
            id: this.$route.query.id,
            title: this.info.title,
            cover: window.unescape(this.info.cover).slice(7)
          }
        ];
      }
      window.console.log(bookrack);
      // 
      localStorage.setItem("bookrack", JSON.stringify(bookrack));
    },
    onLoad() {
      // 
      // immediate-checkv-for
      window.console.log("onLoad");

      Vue.axios(
        "http://novel.kele8.cn/chapters/" +
          this.formating(this.zhangjielist[this.index].link)
      ).then(res => {
        var obj = {
          title: res.data.chapter.title,
          cpContent: res.data.chapter.cpContent.split("\n")
        };
        // 
        this.list.push(obj);
        this.loading = false;

        // 
        this.page = this.index++;

        // url
        if (window.history) {
          // url
          let url = window.location.href;
          // 
          let str = url.slice(0, url.indexOf("index=") + 5 + 1) + this.index;
          // let nextStr = url.slice(url.indexOf("&id"));
          // url
          window.console.log(url);
          window.console.log(str);
          window.history.pushState(null, null, str);
        }

        this.updatabookrack();

        // 
        this.$store.commit("setBook", {
          id: this.$route.query.id,
          value: this.index,
          key: "index"
        });
      });
    },
    formating(str) {
      // : / ? =
      var arr = str.split("");
      for (var i = 0; i < arr.length; i++) {
        if (arr[i] === ":") {
          arr[i] = "%3A";
        }
        if (arr[i] === "/") {
          arr[i] = "%2F";
        }
        if (arr[i] === "?") {
          arr[i] = "%3F";
        }
        if (arr[i] === "=") {
          arr[i] = "%3D";
        }
      }
      var newStr = arr.join("");
      return newStr;
    }
  },
  beforeRouteEnter(to, from, next) {
    window.console.log(document.getElementById("yueduqiLoading"));
    window.console.log("beforeRouteEnter");
    // tofrom$route
    // to
    // from
    window.console.log(to.query.id);
    // 1.0 id
    Vue.axios(
      "http://novel.kele8.cn/book-sources?view=summary&book=" + to.query.id
    )
      .then(res => {
        window.console.log("");
        window.console.time("start")
        // 2.0 link
        Vue.axios(
          "http://novel.kele8.cn/book-chapters/" + res.data[0]._id
        ).then(res2 => {
          window.console.timeEnd("start");
          window.console.log(res2);
          // linknext
          next(vm => {
            window.console.log("beforeRouteEnternext");
            // 2.1 
            vm.zhangjielist = res2.data.chapters;

            // 2.2 
            if (to.query.index) {
              vm.index = to.query.index - 1;
            } else {
              vm.index = 0;
            }
            vm.startloading = false;

            Vue.axios
              .get("http://novel.kele8.cn/book-info/" + to.query.id)
              .then(res3 => {
                vm.info = res3.data;
              });
          });
        });
      })
  },
  beforeRouteLeave(to, from, next) {
    // 
    // 
    // var bookrack = localStorage.getItem('bookrack');
    // if(bookrack){
    //   bookrack.yuedujilu.push({
    //     book:{
    //      name:
    // }
    //     endTime: new Date()
    //   })
    // }
    next();
  },
  created() {
    window.console.log("");
  }
};
</script>

<style lang='less' spcode>
.cataloguejiemian {
  width: 80%;
  height: 100%;
  position: absolute;
  top: 0;
  left: -100%;
  transition: all 0.3s ease;
  &.show {
    left: 0px;
  }
}
.showControl {
  .top {
    top: 0;
  }
  .bottom {
    bottom: 0;
  }
}

.top,
.bottom {
  transition: all 0.3s ease;
}

.top {
  width: 100%;
  position: fixed;
  top: -150px;
}

.bottom {
  width: 100%;
  position: fixed;
  bottom: -500px;
}

.yuedubook {
  width: 100%;
  height: 100%;
  background-color: #eee6dd;

  padding-top: 0.66667rem;
  text-align: left;

  &.night {
    background-color: #0c0c0c;
    h2 {
      color: #888;
    }
    .yuedubook-box {
      p {
        color: #666;
      }
    }
  }

  &.eyeshield {
    background-color: #b8cd8d;
    h2 {
      color: #0c2e10;
    }
    .yuedubook-box {
      p {
        color: rgba(12, 46, 16, 0.8);
      }
    }
  }

  h2 {
    margin: 0px;
    color: #333;
    padding: 0 0.53333rem;
    line-height: 1.5;

    font-size: 24px;
  }

  .yuedubook-box {
    color: #333;
    padding: 0.26667rem 0.53333rem 0;

    p {
      color: #5c5d58;
      // text-indent: 2em;
      text-indent: 1cm;

      line-height: 1.5;
      margin: 0.3333rem 0;
    }
  }

  .erweima {
    width: 100%;
    height: 100px;
  }
}
</style>