<template>
  <div class="xiangqingye">
    <headshot class="head" :title="name" />
    <section class="shuxinxi">
      <div class="shuxinxi_wrapper">
        <div class="left">
          <img :src="cover" alt />
        </div>
        <div class="right">
          <h4>{{data.title}}</h4>
          <div>
            <span>{{data.author}}</span>
            <span>|</span>
            <span>{{data.cat}}</span>
            <span>|</span>
            <span>{{wordCount}}</span>
          </div>
          <span>{{this.data._gg? '' : formating(Date.now() - Date.parse(this.data.updated), false)}}</span>
        </div>
      </div>
    </section>
    <section class="anniu">
      <div class="btn-wrapper">
        <div class="left delete" @click.stop="deleteBookFn" v-show="bookstate"></div>
        <div class="left" @click.stop="addBookFn" v-show="!bookstate"></div>
        <div class="right" @click.stop="goRouteryuedubook"></div>
      </div>
    </section>
    <section class="renqi">
      <div class="item">
        <p></p>
        <span>{{(data.latelyFollower/10000).toFixed(1)}}</span>
      </div>
      <div class="item">
        <p></p>
        <span>{{data.retentionRatio}}%</span>
      </div>
      <div class="item">
        <p>/</p>
        <span>{{data.serializeWordCount}}</span>
      </div>
    </section>
    <section class="juqing">
      <div class="text" :style="{display: jieshao?'block':'-webkit-box'}">{{data.longIntro}}</div>
      <div
        class="jiantou"
        :style="{transform: jieshao? 'rotate(180deg)' : ''}"
        @click.stop="jiantouFn"
      ></div>
    </section>
    <section class="mulu">
      <div class="mulu-box">
        
        <div
          class="right"
          @click.stop="goRouterCataklogueFn(data.title, cover)"
        >{{updateAndprogress}}</div>
      </div>
    </section>

    <!--  -->
    <section class="remenshupin">
      <div class="pinlun">
        <div class="title"></div>
        <comment v-for="(item, index) in pinlun" :key="index" :obj="item" />
      </div>
    </section>

    <!--  -->
    <section class="lingpinlun"></section>

    <!--  -->
    <section class="relate">
      <interestList :obj="relate" />
    </section>

    <!--  -->
    <section class="banquan"></section>

    <!--  -->
    <section class="footer"></section>
  </div>
</template>

<script>
import Vue from "vue";
import headshot from "../components/headshot";
import comment from "../components/comment";
import interestList from "../components/interestList";
export default {
  data() {
    return {
      name: "",
      activeName: "1",
      data: {},
      pinlun: [],
      relate: [],
      jieshao: false
    };
  },
  watch: {
    $route() {
      window.location.reload();
    }
  },
  computed: {
    cover() {
      // slice/agent/
      return window.unescape(this.data.cover).slice(7);
    },
    wordCount() {
      return this.data.wordCount > 9999
        ? (this.data.wordCount / 10000).toFixed(0) + ""
        : this.data.wordCount;
    },
    updateAndprogress() {
      return (
        this.formating(Date.now() - Date.parse(this.data.updated), true) +
        this.data.lastChapter
      );
    },
    bookstate() {
      return this.$store.getters.getBookById(this.$route.query.id);
    }
  },
  components: {
    headshot,
    comment,
    interestList
  },
  created() {
    // 
    Vue.axios
      .get("http://novel.kele8.cn/book/reviews?book=" + this.$route.query.id)
      .then(res => {
        window.console.log("", res);

        window.console.log(res.data.reviews.slice(0, 2));
        this.pinlun = res.data.reviews.slice(0, 2);
      });
    // 
    Vue.axios
      .get("http://novel.kele8.cn/recommend/" + this.$route.query.id)
      .then(res => {
        window.console.log("", res);
        this.relate = res.data.books.slice(0, 4);
      });
  },
  beforeRouteEnter(to, from, next) {
    // info
    Vue.axios
      .get("http://novel.kele8.cn/book-info/" + to.query.id)
      .then(response => {
        window.console.log("", response);
        next(vm => {
          vm.data = response.data;
        });
      });
  },
  methods: {
    formating(str, kuohao) {
      var miao = str / 1000;
      var fen = miao / 60;
      var xiaoshi = fen / 60;
      var tian = xiaoshi / 24;
      var pre = "";
      var next = "";
      if (kuohao) {
        pre = "[";
        next = "] ";
      } else {
        next = "";
      }

      if (tian > 1) {
        return pre + parseInt(tian) + "" + next;
      }

      if (xiaoshi >= 1) {
        return pre + parseInt(xiaoshi) + "" + next;
      } else if (fen >= 1) {
        return pre + parseInt(fen) + "" + next;
      }
    },
    goRouteryuedubook() {
      //  
      var bookrack = JSON.parse(localStorage.getItem("bookrack"));
      var index = 0;
      if (bookrack) {
        for (let i = 0; i < bookrack.length; i++) {
          if (bookrack[i].id === this.$route.query.id) {
            index = bookrack[i].index;
          }
        }
      }

      this.$router.push({
        path: "/yuedubook",
        query: {
          id: this.$route.query.id,
          index: index ? index : 1
        }
      });
    },

    goRouterCataklogueFn(title, cover) {
      this.$router.push({
        path: "/Catalogue",
        query: {
          id: this.$route.query.id,
          title: title,
          cover: cover
        }
      });
    },

    jiantouFn() {
      this.jieshao = !this.jieshao;
    },

    addBookFn() {
      //   ()vuex
      //   ,
      // var bookrack = JSON.parse(localStorage.getItem("bookrack"));
      // var index = null;
      // if (bookrack) {
      //   for (let i = 0; i < bookrack.length; i++) {
      //     if (bookrack[i].id === this.$route.query.id) {
      //       index = bookrack[i].index;
      //     }
      //   }
      // }
      this.$store.commit("addBookrack", {
        //    ()  ()
        // 
        // title: this.data.title,
        // cover: this.cover,
        id: this.$route.query.id
        // index: index ? index : 1,
        // time:
        //   this.formating(Date.now() - Date.parse(this.data.updated), false) +
        //   " : " +
        //   this.data.lastChapter
      });
    },

    deleteBookFn() {
      this.$store.commit("deleteBook", this.$route.query.id);
    }
  }
};
</script>

<style lang="less" scoped>
.shuxinxi {
  width: 100%;
  padding: 15px;
  position: relative;
  .shuxinxi_wrapper {
    width: 100%;
    &::after {
      content: "";
      display: block;
      clear: both;
    }
    .left {
      float: left;
      width: 1.73333rem;
      height: 2.33333rem;
      margin-right: 10px;
      img {
        width: 100%;
        height: 100%;
      }
    }
    .right {
      text-align: left;
      float: left;
      h4 {
        margin: 0;
        padding: 0;
        font-size: 16px;
      }

      & > div {
        margin: 2px 0;

        span:nth-child(1) {
          color: #b93321;
        }

        & > span {
          margin: 0 5px;
          font-size: 12px;
          color: #999;
        }
      }

      & > span {
        font-size: 12px;
        color: #999;
      }
    }
  }
}

// btn
.anniu {
  width: 100%;
  padding: 15px;
  padding-top: 0;
  border-bottom: 1px solid #ebebeb;
  .btn-wrapper {
    width: 100%;
    margin: 0 auto;
    overflow: hidden;
    div {
      width: 45%;
      height: 1.17333rem;
      line-height: 1.17333rem;
    }
    div.left {
      float: left;
      border: 1px solid #b93321;
      color: #b93321;
    }
    div.left.delete {
      background-color: #999;
      color: #fff;
      border: none;
    }
    div.right {
      float: right;
      color: white;
      background-color: #b93321;
    }
  }
}

// 
.renqi {
  width: 100%;
  padding: 15px 0;
  border-bottom: 1px solid #ebebeb;
  .item {
    width: 33%;
    display: inline-block;
    p {
      color: #999;
      font-size: 12px;
      margin: 0;
      padding: 0;
      margin-bottom: 5px;
    }
    span {
      color: black;
      font-size: 16px;
    }
  }
}

// juqing
.juqing {
  width: 100%;
  padding: 0.53333rem;
  position: relative;
  text-align: left;
  .text {
    font-size: 13px;
    width: 100%;
    overflow: hidden;
    // display: -webkit-box;
    -webkit-box-orient: vertical;
    -webkit-line-clamp: 3;
    transition: all 0.3s;
  }
  .jiantou {
    width: 0.53333rem;
    height: 0.53333rem;
    position: absolute;
    right: 10px;
    bottom: 15px;
    background-image: url(../assets/xialalist.png);
    background-size: 60% 60%;
    background-position: center center;
    background-repeat: no-repeat;
  }
  &.active {
    .jiantou {
      transform: rotate(180deg);
    }
    .text {
      // display: block;
      -webkit-line-clamp: none;
    }
  }
}

// 
.mulu {
  width: 100%;
  padding-left: 15px;
  border-bottom: 1px solid #ebebeb;
  .mulu-box {
    width: 100%;
    padding: 15px 0;
    border-top: 1px solid #ebebeb;
    text-align: left;
    .right {
      width: 65%;
      float: right;
      margin-right: 9px;
      position: relative;
      overflow: hidden;
      text-overflow: ellipsis;
      white-space: nowrap;
      color: #999;
      &::after {
        content: "";
        position: absolute;
        right: 0px;
        width: 0.53333rem;
        height: 0.53333rem;
        background-image: url(../assets/xialalist.png);
        background-size: 60% 60%;
        background-position: center;
        background-repeat: no-repeat;
        transform: rotate(270deg);
        margin-left: 10px;
      }
    }
  }
}

// 
.remenshupin {
  width: 100%;
  padding: 15px;
  text-align: left;
  .pinlun {
    width: 100%;
    .title {
      font-size: 16px;
      margin-bottom: 10px;
    }
  }
}
</style>