<template>
  <div class="hot">
    <div class="hot-box">
      <div class="title">
        <div class="title-text"></div>
        <div class="refresh" @click.stop="refreshClickFn">
          
          <!-- afterspan -->
        </div>
      </div>
      <div class="hotkeywords">
        <ul>
          <li
            v-for="(item, index) in arr"
            :key="index"
            @click.stop="routegoSearchListClickFn(item)"
          >{{item}}</li>
        </ul>
      </div>
    </div>
  </div>
</template>
<script>
export default {
  name: "Searchhot",
  data() {
    return {
      hotkeywords: [],
      len: 0,
      arr: [],
      chofu: []
    };
  },
  computed: {},
  methods: {
    refreshClickFn() {
      var arr = [];
      //   60length-1
      for (let i = 0; i < 7; i++) {
        //   
        let random = Math.round(Math.random() * (this.len - 1));
        let isChange = false;
        window.console.log(this.hotkeywords[random]);

        // ,
        if (i > 0) {
          for (let j = 0; j < arr.length; j++) {
            // 
            if (random == arr[j]) {
              // 
              isChange = true;
              //   
              break;
            }
          }
        }

        // 
        // for (let k = 0; k < this.arr.length; k++) {
        //   // 
        //   if (this.arr[k] == this.hotkeywords[random]) {
        //     // 
        //     isChange = true;
        //     // 
        //     break;
        //   }
        // }

        // 
        if (isChange) {
          // 
          // 
          i--;
          continue;
        } else {
          arr.push(random);
        }
      }

      //   
      var list = [];
      for (let i = 0; i < arr.length; i++) {
        list.push(this.hotkeywords[arr[i]]);
      }
      this.arr = list;
    },
    routegoSearchListClickFn(item) {
      // 
      // 
      if (!localStorage.Search) {
        window.console.log("");
        let Search = { KeyWords: [] };
        Search.KeyWords.push(this.value);
        // 
        localStorage.setItem("Search", JSON.stringify(Search));
      } else {
        let Search = JSON.parse(localStorage.getItem("Search"));
        for (let i = 0; i < Search.KeyWords.length; i++) {
          if (item === Search.KeyWords[i]) {
            Search.KeyWords.splice(i, 1);
          }
        }
        Search.KeyWords.push(item);
        // 
        localStorage.setItem("Search", JSON.stringify(Search));
      }

      // 
      this.$router.push({
        path: "/SearchList",
        query: {
          value: item
        }
      });
    }
  },
  created() {
    this.axios.get("http://novel.kele8.cn/hot-books").then(response => {
      this.hotkeywords = response.data.hotWords;
      this.arr = response.data.hotWords.slice(0, 6);
      this.len = response.data.hotWords.length;
    });
  }
};
</script>
<style lang="less" scoped>
.hot {
  width: 100%;
  padding: 0 15px;
  .hot-box {
    width: 100%;
    .title {
      display: flex;
      margin-top: 15px;
      .title-text {
        flex: 1;
        text-align: left;
        margin: 0;
        color: #6b6b6b;
        font-size: 0.58667rem;
      }
      .refresh {
        width: 2.1875rem;
        height: 0.9375rem;
        margin: 0;
        color: #8a8a8f;
        font-size: 0.32rem;
        position: relative;
        line-height: 0.9375rem;
        text-align: center;
        &::after {
          content: "";
          display: block;
          position: absolute;
          right: 0;
          top: 50%;
          margin-top: -0.21333rem;
          width: 0.42667rem;
          height: 0.42667rem;
          background-image: url(../assets/refresh.png);
          background-size: 100% 100%;
          background-repeat: no-repeat;
        }
      }
    }
    .hotkeywords {
      width: 100%;
      ul {
        width: 100%;
        display: flex;
        flex-wrap: wrap;
        li {
          height: 0.8rem;
          line-height: 0.8rem;
          border-radius: 25px;
          padding: 0 10px;
          background-color: #efeff4;
          color: #9b9ba3;
          margin: 5px 0;
          margin-right: 10px;
          font-size: 0.32rem;
        }
      }
    }
  }
}
</style>