<template>
  <div class="search">
    <div class="search-box">
      <div class="content" @click.stop="searchClickFn">
        <div
          :class="[text]"
          :style="{transform: 'translateY(-'+(shanhui? shanhui : searchIndex*speed)+'px)',transition:isTransitionStr}"
        >
          <!-- , {tran:isTransition} -->
          <!--  -->
          <p v-for="(item, index) in hotWords" :key="index">{{item}}</p>
        </div>
        <div class="search-btn" @click.stop="searchBtnClickFn"></div>
      </div>
    </div>
  </div>
</template>
<script>
// import Vue from "vue";
export default {
  data() {
    return {
      // 
      hotWords: [],

      searchIndex: 0,
      speed: 27,
      // 
      isTransitionStr: "all 1s",
      // 
      isTransition: true,
      shanhui: null,
      id: null,
      text: "text"
    };
  },
  computed: {
    searchValue: function() {
      return this.hotWords[this.searchIndex];
    }
  },
  methods: {
    //     
    searchBtnClickFn() {
      this.$router.push({
        path: "/SearchList",
        query: {
          value: this.searchValue
        }
      });
    },
    // input
    searchClickFn() {
      this.$router.push({
        path: "/Search"
      });
    },
    autoplay: function() {
      // (length - 1) lenght- 2
      if (this.searchIndex > this.hotWords.length - 2) {
        // dom
        // this.isTransition = false;
        this.isTransitionStr = "none";
        // 
        this.shanhui = "0";
        // 
        this.searchIndex = 0;
        // 
        window.clearInterval(this.id);
        // ,
        setTimeout(() => {
          // 
          // this.isTransition = true;
          this.isTransitionStr = "all 1s";
          // 
          this.shanhui = null;
          // 
          this.searchIndex++;
          // this.autoplay();
          this.id = setInterval(this.autoplay.bind(this), 3000);
        });
        return;
      } else {
        // 
        this.shanhui = null;
      }

      this.searchIndex++;
    }
  },
  beforeCreate() {
    // 
    this.axios.get("http://novel.kele8.cn/hot-books").then(response => {
      // 
      this.hotWords = response.data.hotWords.slice(0, 6);
      this.hotWords.push(this.hotWords[0]);
    });
  },
  // beforeRouteEnter(to, from, next) {
  //   Vue.axios.get("http://novel.kele8.cn/hot-books").then(response => {
  //     next(vm => {
  //       window.console.log(response.data.hotWords);
  //       vm.hotWords = response.data.hotWords.slice(0, 6);
  //     });
  //   });
  // },
  created() {
    this.id = setInterval(this.autoplay.bind(this), 3000);
  }
};
</script>
<style lang="less">
.search {
  width: 100%;
  height: 34px;
  padding: 3px 6px;
  background-color: white;
  // position: fixed;
  // top: 42px;
  // z-index: 99;
  .search-box {
    width: 100%;
    height: 100%;
    background-color: #e9e9e9;
    border-radius: 5px;
    padding: 5px 10px;
    .content {
      width: 100%;
      height: 100%;
      overflow: hidden;
      position: relative;
      display: flex;
      .text {
        flex: 1;
        height: 100%;
        // CSS3, CSS3  
        // animation: mylunbo 10s ease 0.5s infinite;
        // transition: all 0.3s;
        transform: translateY(0px);
        &.tran {
          transition: all 0.3s;
        }
        p {
          text-align: left;
          font-size: 0.32rem;
          color: #999;
          overflow: hidden;
          text-overflow: ellipsis;
          white-space: nowrap;
          // margin: 15px 0;
        }
      }
      .search-btn {
        width: 0.66667rem;
        height: 0.66667rem;
        background-image: url(../assets/search.png);
        background-repeat: no-repeat;
        background-size: 100% 100%;
      }
    }
  }
}
</style>