<template>
  <div class="ranking">
    <!--  -->
    <Headshot :title="title" />
    <!--  -->
    <div class="justify">
      <van-tabs v-model="active" @click="tabsClickFn">
        <van-tab title="">
          <!--  -->
          <div class="zhanwei">
            <div></div>
          </div>
          <van-sidebar v-model="activeKey">
            <van-sidebar-item
              @click="onclickTab('man', item.shortTitle)"
              v-for="(item, index) in currentArr"
              :key="index"
              :title="item.shortTitle"
            />
            <!-- <van-sidebar-item title="" />
            <van-sidebar-item title="" />
            <van-sidebar-item title="" />
            <van-sidebar-item title="" />
            <van-sidebar-item title="" />
            <van-sidebar-item title="" />
            <van-sidebar-item title="VIP" />
            <van-sidebar-item title="" />
            <van-sidebar-item title="" />-->

            <!--  -->
          </van-sidebar>
          <div class="right">
            <div class="right-box">
              <booksList
                v-for="(item, index) in currentArr2"
                :key="index"
                :book="item"
                :index="index"
              />
            </div>
          </div>
        </van-tab>
        <van-tab title="">
          <!--  -->
          <van-sidebar v-model="activeKey">
            <van-sidebar-item
              @click="onclickTab('nv', item.shortTitle)"
              v-for="(item, index) in currentArr"
              :key="index"
              :title="item.shortTitle"
            />
          </van-sidebar>
          <div class="right">
            <div class="right-box">
              <booksList
                v-for="(item, index) in currentArr2"
                :key="index"
                :book="item"
                :index="index"
              />
            </div>
          </div>
        </van-tab>
        <van-tab title="">
          <!--  -->
          <van-sidebar v-model="activeKey">
            <van-sidebar-item
              @click="onclickTab('chuban', item.shortTitle)"
              v-for="(item, index) in currentArr"
              :key="index"
              :title="item.shortTitle"
            />
             <div class="right">
            <div class="right-box">
              <booksList
                v-for="(item, index) in currentArr2"
                :key="index"
                :book="item"
                :index="index"
              />
            </div>
          </div>
          </van-sidebar>
        </van-tab>
      </van-tabs>
    </div>
  </div>
</template>

<script>
import Headshot from "../components/headshot";
import booksList from "../components/booksList";
import Vue from "vue";
export default {
  data() {
    return {
      title: "",
      active: 0,
      activeKey: 0,
      man: [],
      nv: [],
      chuban: [],
      man2: [],
      nv2: [],
      chuban2: [],
      currentArr: [],
      currentArr2: [],
      tabs: [
        "",
        "",
        "",
        "",
        "",
        "",
        "",
        "VIP",
        ""
      ]
    };
  },
  components: {
    Headshot,
    booksList
  },
  methods: {
    // tab
    tabsClickFn(index, title) {
      window.console.log(title, index);
      if (index === 0) {
        if (this.man2.length > 0) {
          this.currentArr = this.man;
          this.currentArr2 = this.man2[0].books;
        } else {
          this.isdefaut("man");
        }
        // ,created
        // ,id
      } else if (index === 1) {
        // 
        if (this.nv2.length > 0) {
          this.currentArr = this.nv;
          this.currentArr2 = this.nv2[0].books;
        } else {
          this.isdefaut("nv");
        }
      } else if (index === 2) {
        // 
        if (this.chuban2.length > 0) {
          this.currentArr = this.chuban;
          this.currentArr2 = this.chuban2[0].books;
        } else {
          this.isdefaut("chuban");
        }
      }
    },
    // tab
    onclickTab(sex, title) {
      window.console.log(title, sex + 2);
      for (let i = 0; i < this[sex + 2].length; i++) {
        if (this[sex + 2][i].shortTitle === title) {
          this.currentArr2 = this[sex + 2][i].books;
        }
      }
    },
    isdefaut(sex) {
      window.console.log(sex);
      for (let i = 0; i < this[sex].length; i++) {
        Vue.axios
          .get("http://novel.kele8.cn/rank/" + this[sex][i]._id)
          .then(response => {
            // tab
            this[sex + "2"].push(response.data.ranking);
            if (i === 0) {
              this.currentArr2 = this[sex + "2"][i].books;
            }
          });
      }
      // tab
      this.currentArr = this[sex];
    }
  },
  created() {
    window.console.log("created");
  },
  // 
  // this
  beforeRouteEnter(to, from, next) {
    window.console.log("beforeRouteEnter");
    var Search = JSON.parse(localStorage.getItem("Search"));
    //   this,,
    if (Search.Ranking) {
      // 
      next(vm => {
        vm.man = Search.Ranking.man;
        vm.nv = Search.Ranking.nv;
        vm.chuban = Search.Ranking.chuban;
        window.console.log("");
        vm.isdefaut("man");
      });
    } else {
      // 
      var obj = {};

      Vue.axios.get("http://novel.kele8.cn/rank-category").then(response => {
        window.console.log(response);
        // next
        next(vm => {
          // xx
          vm.man = response.data.male;
          vm.nv = response.data.female;
          vm.chuban = response.data.epub;

          obj.man = response.data.male;
          obj.nv = response.data.female;
          obj.chuban = response.data.epub;
          Search.Ranking = obj;
          // 
          localStorage.setItem("Search", JSON.stringify(Search));
          window.console.log("");

          vm.isdefaut("man");
        });
      });
    }
  }
};
</script>

<style lang="less" scoped>
.ranking {
  .justify {
    width: 100%;
    position: fixed;
    top: 1.17333rem;
    left: 0;
    .van-tab__pane {
      display: flex;
      flex-flow: row;
      .zhanwei {
        min-width: 85px;
        height: 100%;
      }
      .van-sidebar {
        position: fixed;
        overflow-y: scroll;
        overflow-x: hidden;

        // topbottom
        top: 81px;
        bottom: 0;
      }

      .right {
        flex-grow: 1;
        .right-box {
          width: 235px;
          // flex-grow
          position: fixed;
          left: 85px;
          top: 81px;
          bottom: 0;
          overflow-y: scroll;
          overflow-x: hidden;
        }
      }
    }
  }
}
</style>